@import 'normalize';
@import 'typography';
@import 'navigation';
@import 'sidebar';
@import 'comments';
@import 'footer';
@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;600&family=Pinyon+Script&display=swap');
/*
====================================
Default Styling
====================================
*/

@mixin fontSize($sizeValue){
    font-size: $sizeValue * 1px;
    font-size: ($sizeValue/10) * 1rem;
}
.flex {
	display: flex;
	flex-flow: row wrap;
}

body {
	font-family: 'Kumbh Sans', sans-serif;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

h1,h2,h3,h4 {
	font-family: 'Pinyon Script', cursive;
}
main {
	max-width: 920px;
	margin: 0 auto;
	padding:0 25px;
}


//home page 

.logo {
	// display: flex;
	// justify-content: center;
	// align-items: center;
	// // height: 800px;

	img {
		max-width: 65vh;
		width: 80%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}


// x home page
.xclickArtist {
	display: none;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1;
	
	.artistImageFramed{
		position: relative;
		top: 0;
		left: 0;
		.image2 {
		  position: absolute;
		  top: -17%;
		  left: -12%;
		  max-width: 500px;
		  width: 125%;

		  
		}
		h2 {
			position:absolute;
			@include fontSize(20);
			color: white;
			text-shadow: 2px 2px 1px #4924ff;
			    top: 30%;
			    left: 2%;
			    width: 90%;
		}
	}


	img.wp-post-image {

		max-width: 400px;
		height: auto;
		width: 80%;
		border:3px solid #4a24fe;
		box-shadow: 1px 1px 7px 2px #e32aba;
		position: relative;
  		top: 0;
  		left: 0;

	}
	text-align: center;
}

.openX{
	display: block;
}
svg {
	position: absolute;
	bottom: -20%;
}
path {
  fill: transparent;
}

text {
  fill: black;
  font-family: 'Pinyon Script', cursive;
  @include fontSize(30);
  text-shadow: -4px 5px 1px #fff;

}
//video

.bg-video-wrap {
  position: fixed;
  overflow: hidden;
  z-index: -1;
  top:0;
  left:0;
  width: 100%;
  height: 100vh;
  background: url(images/background.jpg) no-repeat center center/cover;
}
.bg-video-about {
	background: url(images/aboutbg.jpg) no-repeat center center/cover;
}
video {
	position: absolute;
    top: -9999px;
    bottom: -9999px;
    left: -9999px;
    right: -9999px;
  min-width: 100%;
  min-height: 100vh;
  z-index: 1;
  margin: auto;
}
//Artist page archives

.artistEach {
	a {
		@extend .flex;
		width:100%;
		.artistEachInfo {
			width: 45%;
		}
	}
	
	img {
		max-width: 450px;
		height: auto;
		width: 50%;
		width: 450px;
		margin:0 auto;

	}
	h1 {
		@include fontSize(35);
		color: white;
		text-shadow: #FF2D95 0px 0px 20px, #FF2D95 0px 0px 30px, #FF2D95 0px 0px 50px, -7px 5px 8px #E32ABA;
	}
	h2 {
		@include fontSize(35);
		color:#4a24fe; 
	}
}

//single artist pages

.backgroundtile {
	position: fixed;
	left: 0;
	right: 0;
	z-index: -1;
	display: block;
	width:100%;
	height: 100vh;
filter: sepia(100%) brightness(57%) hue-rotate(199deg) saturate(484%) contrast(1000%);
-webkit-filter: sepia(100%) brightness(57%) hue-rotate(199deg) saturate(484%) contrast(1000%);
-moz-filter: sepia(100%) brightness(57%) hue-rotate(199deg) saturate(484%) contrast(1000%);
}
.singleArtistTile::before {
	filter: grayscale(100%);
}
.artistSingleMain {
	margin: 0 auto;
	width: 65%;
	padding-top: 40px;
	h1 {
		@include fontSize(35);
		color: white;
		text-shadow: #FF2D95 0px 0px 20px, #FF2D95 0px 0px 30px, #FF2D95 0px 0px 50px, -7px 5px 8px #E32ABA;
		margin:0;
	}
	figure {
		width: 100%;
		img {
			max-width: 700px;

			height: auto;
		}
	}
}

.social {
	position: fixed;
	top:50%;
	max-width: 130px;

	a {
		color: white;
		@include fontSize(20);
		margin-right:10px;

	}
}






